@font-face {
  font-family: "Rubik-Medium";
  src: url("../fonts/Rubik-Medium.woff") format("woff"), url("../fonts/Rubik-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Light";
  src: url("../fonts/Rubik-Light.woff") format("woff"), url("../fonts/Rubik-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Regular";
  src: url("../fonts/Rubik-Regular.woff") format("woff"), url("../fonts/Rubik-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SourceSansPro-Regular";
  src: url("../fonts/SourceSansPro-Regular.woff") format("woff"), url("../fonts/SourceSansPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
  max-width: 100%;
  width: 100%;
  font-family: "Rubik-Regular";
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #000000;
  background-color: #ffffff;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
  max-width: 123rem;
}

.link {
  font-family: "Rubik-Medium";
  font-size: 1.5rem;
  line-height: 2.5rem;
}

.link span:nth-of-type(1) {
  padding: 1rem;
  padding-left: 1.5rem;
  color: #000000;
  background-color: #ffffff;
}

.link span:nth-of-type(2) {
  padding: 1rem;
  background-color: transparent;
}

.link svg {
  width: 15px;
  height: 11px;
}

.visually-hidden:not(:active):not(:focus),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  padding-bottom: 21rem;
  color: #ffffff;
  background-color: #000000;
  background-image: linear-gradient(189deg, #000000 80%, #ffffff 20%);
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 7.5rem;
  padding: 2.5rem 0;
}

.navigation__menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 43rem;
  width: 100%;
  line-height: 2rem;
}

.navigation__link:hover {
  border-top: 2px dashed #ffffff;
}

.preview {
  position: relative;
}

.preview__wrapper {
  display: flex;
  justify-content: space-between;
}

.preview__left {
  padding-top: 25rem;
  max-width: 53rem;
  text-align: center;
}

.preview__title {
  margin-bottom: 4rem;
  font-family: "Rubik-Medium";
  font-size: 6rem;
  line-height: 6.5rem;
  color: #ffffff;
}

.preview__description {
  margin-bottom: 3.5rem;
}

.preview__button use {
  fill: #ffffff;
}

.preview__list {
  position: absolute;
  top: 23rem;
  right: 3rem;
}

.social {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 11rem;
  height: 100%;
  max-height: 22rem;
}

.social__link {
  display: flex;
  align-items: center;
}

.social__link:hover .social__text {
  display: inline;
}

.social__link:hover use {
  fill: #ffffff;
}

.social__img {
  width: 25px;
  height: 25px;
  margin-right: 1.5rem;
}

.social__img use {
  fill: #ababab;
  transition: all .15s linear;
}

.social__text {
  display: none;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #ababab;
}